import axios from "axios";
import AWS from 'aws-sdk';


const FRONTEND_API_URL = 'https://a6rsacgln1.execute-api.us-west-2.amazonaws.com/prod/'


axios.defaults.baseURL = FRONTEND_API_URL;
//axios.defaults.headers.common['Access-Control-Allow-Methods'] = 'GET'
//axios.defaults.headers.common['Access-Control-Allow-Headers'] = '*'
//axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*'

const S3_BUCKET ='karin-web-staging-bucket';
const REGION ='us-west-2';

async function updateCode(parameters) {


    const myBucket = new AWS.S3({
        params: { Bucket: S3_BUCKET},
        region: REGION,
    })

    const s3params = {
        Body: JSON.stringify(parameters.entered_code),
        Bucket: S3_BUCKET,
        //Key: 'dev_1/test-problems/test-0/main/java/Code.java'
        Key: parameters.user_id+"/"+parameters.problem_set+"/"+parameters.problem_name+"/main/java/Code.java"
    };

    myBucket.putObject(s3params)
        /*.on('httpUploadProgress', (evt) => {
            setProgress(Math.round((evt.loaded / evt.total) * 100));
        })*/
        .send((err) => {
            if (err) console.log(err)
        });


    const response = await axios({
            method: 'GET',
            url: FRONTEND_API_URL + `%7Bproxy+%7D?code=`+s3params["Bucket"]+`:`+s3params["Key"]+`&classroom_id=testInstructor-testClassroom&request_name=testSubmitCodeToBackend&request_type=submit_code&user=`+parameters["user_id"]+`&request_no=5&problem_id=`+parameters['problem_set']+"-"+parameters["problem_name"],
    });
        //const responseObj = JSON.parse(response);
    const responseObj = response;
    console.log(responseObj);
        //console.log(responseObj.body);
    return responseObj;
        
        /*.then((response) =>{
            console.log(response);
        });*/
}
export {updateCode};