import { useRef, useState } from 'react';
import Card from '../ui/Card';
import classes from "./CodeForm.module.css";
import { updateCode } from '../../services/CodeService';

function CodeForm() {
    const codeInputRef = useRef();
    const [result, setResult] = useState('Console');
    const delay = ms => new Promise(res => setTimeout(res, ms));

    async function submitHandler(event) {
        event.preventDefault();
        const enteredCode = codeInputRef.current.value;
        console.log(enteredCode);

        const params = {
            'user_id' : 'dev_1',
            'problem_set' : 'test-problems',
            'problem_name' : 'test-0',
            'entered_code' : enteredCode
        };
        var response = await updateCode(params);
        var resultString = String(JSON.stringify(response));
        setResult(String(resultString));
        if(resultString.includes('NOT_STARTED', 0)){
            setResult("Server Error, build status: NOT_STARTED");
            /*frontend should never see this status unless something went wrong
            if recieving NOT_STARTED:
                check that files are loaded into s3 properly
                check that information in request matches info sent to s3
            */
        }
        if(resultString.includes('IN_PROGRESS', 0)){
            /*frontend recieves IN_PROGRESS when student build is queued in backend
            builds complete in ~7 seconds, try the same request again after 10 seconds
            if a build is IN_PROGRESS for more than 30 seconds, assume the code failed to build
            */

            
            var loopFlag = true;
            while(loopFlag){
                setResult("Please wait... build status: IN_PROGRESS");
                await delay(15000);
                response = await updateCode(params);
                // eslint-disable-next-line
                resultString = JSON.stringify(response);
                if(resultString.includes('READ', 0)){
                    setResult("Build complete, test results: "+resultString);
                    loopFlag = false;
                }
            }
        }
    }

    var boilerplate = "package main; \n public class Adder { \n\t public int add(int a, int b) { \n // your code goes here \n\t } \n }"

    return (
        <Card>
            <div className={classes.problem}>test-problems/test-0/ <br/> write a function to add two numbers </div>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.coding}>
                    <label htmlFor="code">Code</label>
                    <textarea id="code" required rows="20" ref={codeInputRef} defaultValue={boilerplate}></textarea>
                </div>
                <div className={classes.actions}>
                    <button type="submit">Run</button>
                </div>
            </form>
            <div className={classes.console}>{result}</div>
        </Card>
    );
}
export default CodeForm;