import { Route, Redirect, Switch } from "react-router-dom";
import CodingContestPage from "./pages/CodingContest";
import HomePage from "./pages/Home";
import PracticePage from "./pages/Practice";
import Layout from "./components/layout/Layout";
import AWS from "aws-sdk";
import ClassroomPage from "./pages/Classroom";
import ChatShell from './pages/Chat'

function App() {
  const config = {
    region: "us-west-2",
  };
  AWS.config.update(config);

  return (
    <Layout>
      <Switch>
        <Route path="/health" exact status={200}>
          <h3>The App is Healthy</h3>
        </Route>
        <Route path="/" exact status={200}>
          <HomePage />
        </Route>
        <Route path="/home" exact status={200}>
          <HomePage />
        </Route>
        <Route path="/class" exact status={200}>
          <ClassroomPage />
        </Route>
        <Route path="/code-contest" exact>
          <CodingContestPage />
        </Route>
        <Route path="/practice" exact status={200}>
          <PracticePage />
        </Route>
        <Route path="/discussion" exact status={200}>
          <ChatShell />
        </Route>
        <Route path='/login' component={() => { 
		     window.location.href = 'https://login.auth.codekarin.com/login?response_type=code&client_id=3dt1bt4c3qn04av7h870kn09hj&redirect_uri=https://codekarin.com/home&state=STATE&scope=openid+profile+aws.cognito.signin.user.admin';
		     return null;
		}}/>
        <Route path="/not-found" exact status={200}>
          <h3>Page Not Found</h3>
        </Route>
        <Redirect to="/not-found" />
      </Switch>
    </Layout>
  );
}

//App.use(function)
export default App;
